@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@700&display=swap);
:root {
  --col-dark-0: rgb(0, 14, 29);
  --col-dark-1: rgb(11, 28, 44);
  --col-dark-2: rgb(21, 41, 61);
  --col-dark-3: #2d4863;

  --col-light-0: rgb(217, 230, 247);

  --shadow-wide: 0 0 10px rgb(0, 14, 29, 0.5);
  --shadow-medium: 0 5px 3px rgb(0, 14, 29, 0.5);
}

html,
body {
  height: 100vh;
}

#root,
.App {
  height: 100%;
}

body {
  margin: 0;
  font-family: "Fira Code", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(11, 28, 44);
  background: var(--col-dark-1);
  color: rgb(217, 230, 247);
  color: var(--col-light-0);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 0;
}

header {
  box-sizing: border-box;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  background: rgb(21, 41, 61);
  background: var(--col-dark-2);
  font-size: 2rem;
  padding: 0.4rem 0.8rem;
  box-shadow: 0 0 10px rgb(0, 14, 29, 0.5);
  box-shadow: var(--shadow-wide);
}

footer {
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgb(21, 41, 61);
  background: var(--col-dark-2);
  padding: 0.8rem;
  text-align: center;
  font-size: 0.9rem;
  box-shadow: 0 0 10px rgb(0, 14, 29, 0.5);
  box-shadow: var(--shadow-wide);
}

footer a {
  text-decoration: none;
  color: rgb(217, 230, 247);
  color: var(--col-light-0);
}

footer a:hover {
  text-decoration: underline;
}

.gh-icon-placeholder {
  height: 2rem;
  width: 2rem;
  border: none;
  border-radius: 50%;
  background: rgb(217, 230, 247);
  background: var(--col-light-0);
  color: rgb(21, 41, 61);
  color: var(--col-dark-2);
  text-align: center;
  padding: 0 0 0.1rem 0;
  overflow: hidden;
  font-size: 1.4rem;
  font-family: "Fira Code", monospace;
  cursor: pointer;
}

.gh-icon-placeholder:hover {
  background: rgb(250, 50, 50);
  color: rgb(217, 230, 247);
  color: var(--col-light-0);
}

.btn {
  box-sizing: border-box;
  display: block;
  width: 100%;
  max-width: 36rem;
  margin: 0.5rem 0;
  padding: 0.8rem 1rem;
  font-size: 1.5rem;
  font-family: "Fira Code";
  border: none;
  border-radius: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  background: #2d4863;
  background: var(--col-dark-3);
  box-shadow: 0 5px 3px rgb(0, 14, 29, 0.5);
  box-shadow: var(--shadow-medium);
  color: white;
}

.game-container {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 3rem 1rem;
}

.game-active {
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100%;
  width: 100%;
}

.game-play-container {
  box-sizing: border-box;
  width: 100%;
}

.score-board {
  text-align: center;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2rem;
}

.score-board__current-score {
  font-size: 4.5rem;
}

.word-selector {
  width: 100%;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.game-over {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  max-width: 40rem;
  font-size: 1.2rem;
  text-align: center;
  overflow-y: none;
}

.game-over__word-list-spacer {
  box-sizing: border-box;
  border: 2px solid white;
  border-radius: 15px;
  width: 100%;
  margin: 1rem 0;
}

.game-over__word-list {
  max-height: 30vh;
  overflow-y: scroll;
  padding-left: 3em;
  margin: 0.5rem 0;
  font-size: 1.3rem;
  text-align: left;
}

.game-over__score {
  font-size: 4rem;
}
/* very small widths */
@media only screen and (max-width: 24rem) {
  .btn,
  .game-over {
    font-size: 1.2rem;
  }
  .game-over__score {
    font-size: 2.5rem;
  }
}

